}

::placeholder {
  color: #4E66C2;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Satoshi", sans-serif;
  font-size: 1.6rem;
  overflow-x: hidden;
}
body.dark-mode .contenedor_main::-webkit-scrollbar-track {
  background-color: #CCD8F1;
}
body.dark-mode .contenedor_main::-webkit-scrollbar-thumb {
  background-color: #6280CF;
}
body.dark-mode .sidebar {
  background-color: #4557B5;
}
body.dark-mode .sidebar .menu_item {
  background-color: #4557B5;
  border: 0.25rem solid #7684BC;
}
body.dark-mode .app .gen_card_item,
body.dark-mode .app .gen_stat_item {
  box-shadow: none;
  background-color: #4557B5;
}
body.dark-mode .app .gen_card_item .gen_card_item_desc,
body.dark-mode .app .gen_card_item .gen_card_item_amount {
  color: #F2F5FC;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

td {
  padding: 0.5rem 2rem;
}

input {
  width: 100%;
}

input:focus,
select:focus {
  outline: 0.1rem solid #4E66C2;
}

::-webkit-scrollbar {
  width: 1.5rem;
}

::-webkit-scrollbar-track {
  background-color: #A9C0E7;
  border-radius: 10rem;
}

::-webkit-scrollbar-thumb {
  background-color: #4E66C2;
  border-radius: 10rem;
}

@keyframes show_slide {
  0% {
    transform: translateX(100%);
  }
  40%, 50%, 60%, 70%, 80%, 90% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(100%);
    display: none;
  }
}
.transition {
  transition: opacity 0.5 ease-in-out;
}

.dark-mode .title_page {
  color: #F2F5FC;
}
.dark-mode .description_page {
  color: #F2F5FC;
  font-weight: 700;
}

h1,
h2,
h3 {
  font-family: "Satoshi", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  font-size: 2rem;
  margin: 0;
}

.title_page {
  margin: 2rem;
  text-align: center;
  font-weight: 900;
  color: #6280CF;
}
.title_page span {
  display: block;
  font-size: 6rem;
}

.description_page {
  margin: 2rem;
  text-align: center;
  font-size: 2.5rem;
}

.app.dark-mode .contenedor_app {
  background-color: #4557B5;
}
.app.dark-mode .contenedor_app .contenedor_header .app_title {
  color: #F2F5FC;
}

.contenedor_app {
  min-height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.contenedor_app .contenedor_header {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
  width: 100%;
  gap: 2rem;
  padding: 2rem;
}
.contenedor_app .contenedor_header .contenedor_header_head .app_title {
  text-align: left;
  color: #4E66C2;
  margin: 0;
}
.contenedor_app .contenedor_header .contenedor_header_head .app_boton {
  font-size: 1.5rem;
}
.contenedor_app .contenedor_header .contenedor_header_search input::placeholder {
  font-size: 1.8rem;
}
.contenedor_app .contenedor_main {
  overflow: auto;
  margin: 0 2rem;
}
@media (min-width: 768px) {
  .contenedor_app .contenedor_header {
    flex-direction: row;
  }
  .contenedor_app .contenedor_header_head {
    flex: 3;
  }
  .contenedor_app .contenedor_header_search {
    flex: 1;
  }
  .contenedor_app .app_boton {
    max-width: 25rem;
  }
  .contenedor_app .contenedor_main {
    max-height: 40rem;
    overflow: auto;
  }
}

.app_title {
  color: red;
}

.app.dark-mode .table_head {
  background-color: #6280CF;
}
.app.dark-mode .table_row {
  background-color: #E2E9F7;
}
.app.dark-mode .table_row td {
  border: none;
}

.table {
  width: 100rem;
  table-layout: fixed;
}
@media (min-width: 768px) {
  .table {
    width: 100%;
  }
}

.table_head {
  position: sticky;
  top: 0;
  background-color: #809FDA;
  color: #F2F5FC;
}

.table_head_item th {
  padding: 2rem;
}

.table_head_item {
  padding: 1.5rem 0;
}

.table_head_item,
.table_row td {
  padding: 1.5rem;
  overflow: hidden;
  white-space: nowrap;
  font-weight: 500;
  text-overflow: ellipsis;
  text-align: center;
  border: 1px solid #809FDA;
}

.table_row {
  background-color: #F2F5FC;
}

.table_actions {
  cursor: pointer;
  border: none;
  outline: none;
  font-size: 2rem;
  background: none;
}
.table_actions.edit {
  color: #4E66C2;
}
.table_actions.delete {
  color: #fb4747;
}

@media (min-width: 768px) {
  .contenedor {
    display: grid;
    grid-template-columns: 0.5fr 2fr;
    column-gap: 1rem;
    min-height: 100vh;
  }
}
.contenedor .sidebar {
  background-color: #343F74;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 4rem;
  align-items: center;
  padding: 4rem 0rem;
  border-radius: 0 2rem 2rem 0;
  margin: 1rem 0;
  position: relative;
}
.contenedor .sidebar .sidebar_image {
  max-width: 100%;
}
.contenedor .sidebar .sidebar_image img {
  border-radius: 50%;
}
.contenedor .sidebar .menu_links {
  width: 100%;
  margin: 0;
  padding: 0 2rem;
  list-style-type: none;
}
.contenedor .sidebar .menu_item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 5rem;
  margin-top: 1.5rem;
  padding: 0 2rem;
  border-radius: 1.5rem;
  background-color: #7684BC;
  list-style: none;
}
.contenedor .sidebar .menu_item.active {
  background-color: #F2F5FC;
}
.contenedor .sidebar .menu_item.active .menu_link,
.contenedor .sidebar .menu_item.active .menu_icon {
  cursor: pointer;
  color: #4E66C2;
}
.contenedor .sidebar .menu_item .menu_link {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  font-weight: 700;
  color: #F2F5FC;
}
.contenedor .sidebar .menu_item .menu_icon {
  color: #F2F5FC;
}
.contenedor .sidebar_footer {
  width: 100%;
}
.contenedor .sidebar_footer .user {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 2rem;
}
.contenedor .sidebar_footer .user button {
  all: unset;
}
.contenedor .sidebar_footer .user .user_logout {
  color: #F2F5FC;
  font-size: 2.5rem;
  transform: rotate(180deg);
  cursor: pointer;
}
.contenedor .sidebar_footer .user .user_name {
  color: #F2F5FC;
}
.contenedor .app {
  background-color: #F2F5FC;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .login_image {
  height: 20rem;
  background-image: url(../img/LoginImage.jpeg);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .login_image {
    height: auto;
  }
}
.contenedor-app .login_session {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}

.gen_contenedor {
  min-height: 100%;
  min-width: 100%;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  gap: 2rem;
  padding: 2rem;
}
.gen_contenedor .gen_card_item,
.gen_contenedor .gen_stat_item {
  border-radius: 2rem;
  box-shadow: 0px 4px 20px rgba(49, 102, 148, 0.563);
}
.gen_contenedor .gen_cards {
  min-height: 100%;
  min-width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}
.gen_contenedor .gen_cards .gen_card_item {
  min-height: 100%;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}
.gen_contenedor .gen_cards .gen_card_item .gen_card_item_image {
  width: 15rem;
}
.gen_contenedor .gen_cards .gen_card_item .gen_card_item_amount {
  color: #4E66C2;
  font-weight: 900;
  font-size: 6rem;
}
.gen_contenedor .gen_cards .gen_card_item .gen_card_item_desc {
  color: rgba(0, 0, 0, 0.5019607843);
  font-size: 3rem;
  font-weight: 900;
}
.gen_contenedor .gen_statistics {
  min-height: 100%;
  min-width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
.gen_contenedor .gen_statistics .gen_stat_item {
  min-height: 100%;
  min-width: 100%;
}
@media (min-width: 768px) {
  .gen_contenedor .gen_cards {
    grid-template-columns: repeat(3, 1fr);
  }
}

.not_found {
  min-width: 100vw;
  min-height: 100vh;
  background-image: url(../img/not_found2.webp);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 2rem;
}
.not_found .boton {
  max-width: 30rem;
  padding: 1rem 2rem;
  margin-bottom: 10rem;
}

.dark-mode .form {
  background-color: #F2F5FC;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin: 1rem 2rem;
  padding: 4rem;
  border-radius: 2rem;
  border: 1px solid #4E66C2;
}
.form .button_close_form {
  position: absolute;
  top: 2rem;
  right: 2rem;
  font-size: 2.5rem;
  color: #4E66C2;
  cursor: pointer;
}
.form .campo_doble {
  display: flex;
  gap: 2rem;
}
.form .campo_label {
  color: #4E66C2;
  font-weight: 700;
  font-size: 2.4rem;
  text-transform: capitalize;
}
.form .campo_desc {
  color: #4E66C2;
  font-weight: 300;
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
}
.form .campo_simple {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}
.form .campo_simple input,
.form .campo_simple select {
  font-size: 2rem;
  width: 100%;
  padding: 1rem 2rem;
  background-color: #F2F5FC;
  border: none;
  box-shadow: 1px 4px 20px rgba(52, 63, 116, 0.3);
  color: #4E66C2;
  border-radius: 1.5rem;
}
.form .boton {
  padding: 1rem 2rem;
}

.formulario_login {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.campo {
  width: 100%;
  display: flex;
  margin-bottom: 3rem;
  box-shadow: 1px 4px 20px rgba(52, 63, 116, 0.4);
  border-radius: 1.5rem;
  padding: 1.5rem 2rem;
  background: #F2F5FC;
}
.campo i {
  color: #4E66C2;
}
.campo input {
  border: none;
  background-color: #F2F5FC;
}
.campo input:focus {
  outline: none;
}

.form_contenedor {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
  align-items: center;
  height: 100%;
}
@media (min-width: 768px) {
  .form_contenedor {
    position: inherit;
  }
}

.campo_search {
  justify-content: space-between;
  align-items: center;
}

